import { call, all, takeLatest, fork, put } from "redux-saga/effects";
import {
  USER_ID_FAILURE,
  USER_ID_SUCCESS,
  USER_ID_REQUEST,
  USER_JOIN_REQUEST,
  USER_JOIN_SUCCESS,
  USER_JOIN_FAILURE,
} from "../reducer/R_user_join";
import { http } from "../lib/http";

//   
function* IDcheck(action) {
  const result = yield call(idexsits, action.data); // idexsits(action.data);  
  if (result.status === 204) {
    yield put({
      type: USER_ID_SUCCESS,
      data: result.status,
    });
  } else {
    yield put({
      type: USER_ID_FAILURE,
      data: result.status,
    });
  }
}

//   select   (  )
async function idexsits(data) {
  return await http
    .get("/member/normal/id", {
      params: {
        uid: data,
      },
    })
    .then((response) => {
      return response;
    })
    .catch((error) => {
      return error.response;
    });
}

//  
function* UserSignUp(action) {
  const result = yield call(SignUp, action.data);
  if (result.status === 204) {
    yield put({
      type: USER_JOIN_SUCCESS,
      data: result.status,
    });
  } else {
    yield put({
      type: USER_JOIN_FAILURE,
      data: result.status,
    });
  }
}

//    
async function SignUp(data) {
  return await http
    .post("/member/normal/signup", data)
    .then((response) => {
      return response;
    })
    .catch((error) => {
      return error.response;
    });
}

function* USER_ID() {
  yield takeLatest(USER_ID_REQUEST, IDcheck);
}

function* USER_JOIN() {
  yield takeLatest(USER_JOIN_REQUEST, UserSignUp);
}

export default function* S_user_join() {
  yield all([fork(USER_ID), fork(USER_JOIN)]);
}
