import axios from 'axios';

// axios instance 
export const http = axios.create({
  baseURL: "http://localhost:8080", //  
	withCredentials: true,
});

// axios  resquest  
http.interceptors.request.use(
  (config) => {
    // axios  post, delete, put, patch     (csrf   )
    if (
      config.method === "post" ||
      config.method === "delete" ||
      config.method === "patch"||
      config.method === "put") {
        
        // CTK    
        if (getCookie('CTK') === undefined) {
        //     
        const RandomText = generateRandomString();
        setCookie('CTK', encodeURIComponent(RandomText));
        }
        //   CTK       
        //   cookie       Double Submit Cookie 
        const csrfToken = getCookie('CTK').substring(4, 64) + "S1e2rfaSDASXx3sx631s1RVGcQsFEWZX5S11a2FdaT22fwLOa32q3";
        config.headers.Checktoken = csrfToken;
        return config;
      }
      return config; 
    },
  (error) => Promise.reject(error)
);

// csrf      
const generateRandomString = () => {
  var text = "";
  var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  for(var i = 0; i < 60; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
  }
  return text;
}

//   
const setCookie = (cname, cvalue) => {
  document.cookie = cname + "=" + cvalue + ";path=/";
}

//   
const getCookie = (cname) => {
  let cookies = document.cookie.split(';');
  for (let i in cookies) {
    //   -1 .
    if (cookies[i].indexOf(cname) > -1) {
      return `${cookies[i]};`;
    }
  }
}

// axios  response  
http.interceptors.response.use(
  // 200      
  function (response) {
    return response;
  },
  //   
  async (error) => {
    //    
    const { config, response: { status }} = error;
    // error   Access    
    if (status === 400) {
      if (error.response.data.message === " .") {

        //    
        const originalRequest = config;

        //   
        await http.post("/member/normal/reissue");

        //        
        return axios(originalRequest);
      }
    }
    // Access   error  
    return Promise.reject(error);
  }
);